import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'
import {start, Start, rainbow, StyledText, Content, ContentGroup, Parent, Sub, Body, BodyImage, StyledLink, Glow} from './styles.js'

class GinaK extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false,
      hover3: false,
      hover4: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
    console.log(this.state.hover)
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
    console.log(this.state.hover)
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <p>
                <Glow>
                Gina K Acupuncture
                </Glow>

                <Sub>
                Dec 2020
                </Sub>

                <Body>
                I was commissioned to do a rebranding for Gina K Acupuncture which included a logo, medical labels, letterhead, amongst other collateral assets.
                </Body>
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default GinaK



